'use client';

import { useState, useEffect } from 'react';
import { Message, Conversation } from '../types';
import { useAuth } from '../hooks/useAuth';
import { api } from '../services/api';
import { ChatMessage } from '../components/ChatMessage';
import { ConversationList } from '../components/ConversationList';
import router from 'next/router';

export default function ChatPage() {
  const { user, loading, isAuthenticated } = useAuth();
  const [messages, setMessages] = useState<Message[]>([]);
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [selectedConversation, setSelectedConversation] = useState<string>('');
  const [newMessage, setNewMessage] = useState('');

  useEffect(() => {
    if (isAuthenticated) {
      loadConversations();
    } else {
      router.push('/login');
    }
  }, [isAuthenticated]);

  useEffect(() => {
    if (selectedConversation) {
      loadMessages(selectedConversation);
    }
  }, [selectedConversation]);

  const loadConversations = async () => {
    try {
      const data = await api.getConversations();
      setConversations(data);
      if (data.length > 0 && !selectedConversation) {
        setSelectedConversation(data[0].id);
      }
    } catch (error) {
      console.error('Error loading conversations:', error);
    }
  };

  const loadMessages = async (conversationId: string) => {
    try {
      const data = await api.getMessages(conversationId);
      setMessages(data);
    } catch (error) {
      console.error('Error loading messages:', error);
    }
  };

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newMessage.trim() || !selectedConversation) return;

    try {
      const message: Partial<Message> = {
        conversationId: selectedConversation,
        content: newMessage,
        sender: localStorage.getItem('userId') || '',
        isRead: true,
        isModified: false,
        isDeleted: false,
      };

      const sentMessage = await api.sendMessage(message);
      setMessages(prev => [...prev, sentMessage]);
      setNewMessage('');
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };

  if (loading) {
    return <div className="flex items-center justify-center h-screen">Loading...</div>;
  }

  if (!isAuthenticated) {
    return null;
  }

  return (
    <div className="flex h-screen bg-gray-100">
      <ConversationList
        conversations={conversations}
        selectedId={selectedConversation}
        onSelect={setSelectedConversation}
      />

      <div className="flex-1 flex flex-col">
        <div className="bg-white shadow-sm p-4 border-b border-gray-200">
          <h1 className="text-xl font-bold text-gray-900">
            {conversations.find(c => c.id === selectedConversation)?.name}
          </h1>
        </div>

        <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-gray-50">
          {messages.map((message) => (
            <ChatMessage
              key={message.id}
              message={message}
              isOwnMessage={message.sender === 'user1'} // TODO: Use actual user ID
            />
          ))}
        </div>

        <form onSubmit={handleSendMessage} className="bg-white p-4 border-t border-gray-200">
          <div className="flex space-x-2">
            <input
              type="text"
              value={newMessage}
              onChange={(e) => setNewMessage(e.target.value)}
              placeholder="Type your message..."
              className="flex-1 px-4 py-2 border border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
            <button
              type="submit"
              className="bg-blue-600 text-white px-6 py-2 rounded-full hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
            >
              Send
            </button>
          </div>
        </form>
      </div>
    </div>
  );
} 